@use "../import"as f;

body {
	font-family: 'Noto Sans JP',
		"Helvetica Neue",
		"Segoe UI",
		"Hiragino Kaku Gothic ProN",
		"Hiragino Sans",
		Meiryo, sans-serif;
	background: #fff;
	position: relative;
	color: f.$colorBlack;
}

p {
	// @include f.vw(40);
	// @include f.rem(30);
	@include f.viewScale(font-size, 430px, 16px, 768px, 18px);
	@include f.viewScale(margin, 430px, 10%, 768px, 20%);

}



button {
	font-family: 'Noto Sans JP', sans-serif;
}

/* Page Loader */
.js-loader {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;

	&::before {
		content: '';
		position: fixed;
		z-index: 110;
		top: 0;
		left: 0;
		display: flex;
		width: 100%;
		height: 100%;
		text-align: center;
		background: #707c8c;
		pointer-events: none;
		transition: all .5s ease-in;

	}

	&::after {
		content: 'welcome';
		position: fixed;
		z-index: 110;
		top: 50%;
		left: 50%;
		width: 50px;
		height: 50px;
		border-radius: 50px;
		margin: -25px 0 0 -25px;
		pointer-events: none;
		color: #fff;
		transition: all .5s ease-in;

	}
}

.js-loader.is-loaded {
	position: unset;

	&::before {
		opacity: 0;
		transition: all .5s ease-in;
		z-index: -1;
	}

	&::after {
		opacity: 0;
		transition: all .3s ease-in;
		z-index: -1;
	}
}

section {
	height: 100vh;
	display: flex;
	justify-content: space-around;
	align-items: center;
	background-color: aquamarine;
}

.js-fadein {
	display: inline-block;
	margin: 0 20px;
	height: 60px;
	width: 60px;
	background: #888;
	opacity: 0;
}