@charset "UTF-8";
body {
  font-family: "Noto Sans JP", "Helvetica Neue", "Segoe UI", "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  background: #fff;
  position: relative;
  color: #202020;
}

p {
  font-size: 16px;
  margin: 10%;
}
@media screen and (min-width: 430px) {
  p {
    font-size: calc(0.5917159763vw + 13.4556213018px);
  }
}
@media screen and (min-width: 768px) {
  p {
    font-size: 18px;
  }
}
@media screen and (min-width: 430px) {
  p {
    margin: calc(2.9585798817vw + -2.7218934911%);
  }
}
@media screen and (min-width: 768px) {
  p {
    margin: 20%;
  }
}

button {
  font-family: "Noto Sans JP", sans-serif;
}

/* Page Loader */
.js-loader {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.js-loader::before {
  content: "";
  position: fixed;
  z-index: 110;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  text-align: center;
  background: #707c8c;
  pointer-events: none;
  transition: all 0.5s ease-in;
}
.js-loader::after {
  content: "welcome";
  position: fixed;
  z-index: 110;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  margin: -25px 0 0 -25px;
  pointer-events: none;
  color: #fff;
  transition: all 0.5s ease-in;
}

.js-loader.is-loaded {
  position: unset;
}
.js-loader.is-loaded::before {
  opacity: 0;
  transition: all 0.5s ease-in;
  z-index: -1;
}
.js-loader.is-loaded::after {
  opacity: 0;
  transition: all 0.3s ease-in;
  z-index: -1;
}

section {
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: aquamarine;
}

.js-fadein {
  display: inline-block;
  margin: 0 20px;
  height: 60px;
  width: 60px;
  background: #888;
  opacity: 0;
}

.header {
  min-height: 100vh;
  /*for IE11*/
  min-height: calc(var(--vh, 1vh) * 100);
  background-image: url(https://drive.google.com/uc?export=view&id=1rZVg3bV-CFxvavnLTPvJ5HlIe0S_mbVn);
  /*↑ここまでが100vhの設定*/
  display: grid;
  place-items: center;
}

.header-title {
  background-color: #fff;
  display: inline-block;
  font-size: 3vw;
  padding: 1vw;
}

